import globalData from './globalData'

const i18n = function (language,cb) {
    var cn = {
            //--common--//
            'control':'',
            'local':'',
            'remote':'',
            '_lang': 'cn',
            'lang': '',
            'title': 'Cassia ',
            'header': 'cassia ',
            'reboot': '',
            'allApi': 'API',
            'scanDevice': '',
            'connDevice': '',
            'connedDevice': '',
            'discoverServices': '',
            'openNotify': 'Hub',
            'stateChange': '',
            'writeCom': '',
            'disCon': '',
            'scanList': '',
            'startScan': '',
            'devcieAndService': '',
            'getList': '',
            'stateChangeNotify': '',
            'notifyList': '',
            'openHubNotify': 'Hub',
            'clearList': '',
            'apiSocket': 'API',
            'scanResult': '',
            'disService': '',
            'getMsg': '',
            'deviceConStateChange': '',
            'arguments': '',
            'optional': '()',
            'required': '()',
            'description': '',
            'hubNotifyStatus': 'Hub',
            'method': '',
            'addMore': '',
            'username':':',
            'password':':',
            'host':'AC :',
            'connectedNum':': ',
            'oAouh-Tip-p1':'<b>URL</b>URLAPI',
            //'oAouh-Tip-p2':'<b></b>oAuth2.0base641access_token,access_tokenAPI',
            'oAouh-Tip-p3':'<b>/</b>Cassia(base64)',
            'oAouh-Tip-p4':'<b>AC Address</b>'
        },
        en = {
            'control':'control',
            'local':'local',
            'remote':'remote',
            '_lang': 'en',
            'lang': 'Language',
            'title': 'Cassia bluetooth Dev Tools',
            'header': 'Cassia bluetooth Dev Tools',
            'reboot': 'Reboot',
            'allApi': 'API Info',
            'scanDevice': 'Scan Device',
            'connDevice': 'Connect Device',
            'connedDevice': 'Connected Devices',
            'discoverServices': 'Discover Device Services',
            'openNotify': 'Open Hub Notify',
            'stateChange': 'Connection State Changes',
            'writeCom': 'Write Instruction',
            'disCon': 'Disconnect',
            'scanList': 'Scan List',
            'startScan': 'Start Scan',
            'devcieAndService': 'Device and Services List',
            'getList': 'Connected Devices',
            'stateChangeNotify': 'Connection State Changes Notify',
            'notifyList': 'Notify List',
            'openHubNotify': 'Open Hub Notify',
            'clearList': 'Clear List',
            'apiSocket': 'Api Interfaces',
            'scanResult': 'Scan Results',
            'disService': 'Discover Services',
            'getMsg': 'Devices\'s Messages',
            'deviceConStateChange': 'Devices Connection State Changes',
            'arguments': 'Parameter',
            'optional': '(optional)',
            'required': '(Required)',
            'description': 'Description',
            'hubNotifyStatus': 'Hub Notify Status',
            'method': 'Method',
            'addMore': 'Add More',
            'username':'Developer Key:',
            'password':'Developer Secter:',
            'host':'AC Address:',
            'connectedNum':'connected number: ',
            'oAouh-Tip-p1':'<b>Interface URL:</b>calling the interface, this URL is suyomatically generated in the window below "API Interface".',
            'oAouh-Tip-p2':'<b>Interface Description:</b>This interface is achieved through oAuth2.0 cloud remote control. The developer key and developer secter to base64 encoding added in the request parameters, access to 1hour after the successful authentication access_token, you can add parameters access_token access other API, in order to achieve remote control.',
            'oAouh-Tip-p3':'<b>Parameter Explanation:Developer Key / Developer Secter:</b>Developer account and password requested form Cassia (will be added as a base64 encoding in the request).',
            'oAouh-Tip-p4':'<b>AC Address: </b>The address of the AC that interacts with the Bluetooth router.'
        },
        lang = {},
        i18n = function (k) {
            return lang[k] || null;
        },
        auto = function () {
            var bl = (navigator.language || navigator.browserLanguage).toLowerCase();
            bl.match('cn') ? (lang = cn) : (lang = en);
        };

    /* auto select language form settings */
    if (typeof language === 'undefined') {
        try {
            var s = JSON.parse(localStorage.getItem('settings'));
            if (!s.language || s.language === 0) {
                auto();
            } else {
                (s.language === 'cn') ? (lang = cn) : (lang = en);
            }
        } catch (e) {
            auto()
        }
    } else if (language === 'cn') {
        lang = cn
    } else {
        lang = en
    }

    i18n.render = function () {
        globalData.lang = lang._lang
        console.warn(globalData.lang)

        $('#lang option').removeAttr('checked')
        $('#lang').val(globalData.lang)

        setTimeout(function () {
            var a = document.getElementsByTagName('*'),
                t, s;
            for (var i in a) {
                t = a[i];
                if (t && t.getAttribute) {
                    s = t.getAttribute('i18n');
                    // console.log('#######', t.getAttribute('i18n-loaded') !== globalData.lang)
                    if (s && i18n(s) && t.getAttribute('i18n-loaded') !== globalData.lang) {
                        t.innerHTML = i18n(s);
                        t.setAttribute('i18n-loaded', globalData.lang);
                    }
                }
            }
            cb&&cb()
        }, 25);
    };
    i18n.render();
    return i18n
}

export default i18n


